// initial curated synergies (small sample). Will be editable in UI and persisted to localStorage.
const SYNERGY_BASE_INITIAL = {
  "ursa": ["shadow_shaman"],
  "juggernaut": ["lion", "dazzle"],
  "axe": ["crystal_maiden"],
};

// helper: default order generator (6 bans alternating, then 10 picks alternating starting Radiant)
const DEFAULT_ORDER = [
  "ban:radiant", // 1
  "ban:dire",    // 2
  "ban:dire",    // 3
  "ban:radiant", // 4
  "ban:dire",    // 5
  "ban:dire",    // 6
  "ban:radiant", // 7
  "pick:radiant",// 8
  "pick:dire",   // 9
  "ban:radiant", //10
  "ban:radiant", //11
  "ban:dire",    //12
  "pick:dire",   //13
  "pick:radiant",//14
  "pick:radiant",//15
  "pick:dire",   //16
  "pick:dire",   //17
  "pick:radiant",//18
  "ban:radiant", //19
  "ban:dire",    //20
  "ban:dire",    //21
  "ban:radiant", //22
  "pick:radiant",//23
  "pick:dire",   //24
];

function useLocalStorage(key, initialValue) {
  const [storedValue, setStoredValue] = useState(() => {
    try {
      const item = window.localStorage.getItem(key);
      return item ? JSON.parse(item) : typeof initialValue === 'function' ? initialValue() : initialValue;
    } catch (error) {
      console.warn(error);
      return typeof initialValue === 'function' ? initialValue() : initialValue;
    }
  });
  const setValue = (value) => {
    try {
      const valueToStore = value instanceof Function ? value(storedValue) : value;
      setStoredValue(valueToStore);
      window.localStorage.setItem(key, JSON.stringify(valueToStore));
    } catch (error) {
      console.warn(error);
    }
  };
  return [storedValue, setValue];
}

function initials(name) {
  return (
    (name || "")
      .replace(/_/g, ' ')
      .split(' ')
      .filter(Boolean)
      .map((s) => s[0])
      .join("")
      .slice(0, 3)
      .toUpperCase()
  );
}

export default function CaptainsModeDraft() {
  // hero data
  const [heroes, setHeroes] = useLocalStorage("cm.heroes", FALLBACK_HEROES);

  // bans and picks state
  const [radiantBans, setRadiantBans] = useLocalStorage("cm.radiantBans", []);
  const [direBans, setDireBans] = useLocalStorage("cm.direBans", []);
  const [radiantPicks, setRadiantPicks] = useLocalStorage("cm.radiantPicks", []);
  const [direPicks, setDirePicks] = useLocalStorage("cm.direPicks", []);
  // history for undo (array of { action: 'ban'|'pick', team: 'radiant'|'dire', hero: id })
  const [history, setHistory] = useLocalStorage("cm.history", []);

  // synergy base editable in UI
  const [synergyBase, setSynergyBase] = useLocalStorage("cm.synergyBase", SYNERGY_BASE_INITIAL);

  // order editor state (array of strings like 'ban:radiant' or 'pick:dire')
  const [order, setOrder] = useLocalStorage("cm.order", () => DEFAULT_ORDER);
  const [orderEditorOpen, setOrderEditorOpen] = useState(false);

  // modal state for editing synergies
  const [synergyEditorOpen, setSynergyEditorOpen] = useState(false);
  const [editingHero, setEditingHero] = useState(heroes[0]?.id || "");

  // helper sets
  const takenIds = useMemo(() => new Set([...radiantBans, ...direBans, ...radiantPicks, ...direPicks]), [radiantBans, direBans, radiantPicks, direPicks]);

  // dynamically build a SYNERGY_MAP that includes every hero currently in the pool
  const SYNERGY_MAP = useMemo(() => {
    const map = {};
    for (const h of heroes) {
      map[h.id] = Array.isArray(synergyBase[h.id]) ? [...synergyBase[h.id]] : [];
    }
    return map;
  }, [heroes, synergyBase]);

  // current step index driven by history length
  const stepIndex = history.length; // 0-based
  const currentOrderItem = order[stepIndex];
  const currentAction = currentOrderItem ? currentOrderItem.split(":")[0] : null; // 'ban' | 'pick' | null
  const currentTeam = currentOrderItem ? currentOrderItem.split(":")[1] : null; // 'radiant' | 'dire' | null

  function countPicksForTeam(team) {
    return team === "radiant" ? radiantPicks.length : direPicks.length;
  }

  // assign hero according to order[stepIndex]
  function assignHero(heroId) {
    if (!currentOrderItem) return; // draft finished
    if (takenIds.has(heroId)) return;
    const [type, team] = currentOrderItem.split(":");
    if (type === 'ban') {
      if (team === 'radiant') {
        setRadiantBans((prev) => [...prev, heroId]);
        setHistory((h) => [...h, { action: 'ban', team: 'radiant', hero: heroId }]);
      } else {
        setDireBans((prev) => [...prev, heroId]);
        setHistory((h) => [...h, { action: 'ban', team: 'dire', hero: heroId }]);
      }
      return;
    }
    if (type === 'pick') {
      if (team === 'radiant') {
        if (radiantPicks.length >= 5) return;
        setRadiantPicks((prev) => [...prev, heroId]);
        setHistory((h) => [...h, { action: 'pick', team: 'radiant', hero: heroId }]);
      } else {
        if (direPicks.length >= 5) return;
        setDirePicks((prev) => [...prev, heroId]);
        setHistory((h) => [...h, { action: 'pick', team: 'dire', hero: heroId }]);
      }
      return;
    }
  }

  // undo last action
  function undoLast() {
    if (history.length === 0) return;
    const last = history[history.length - 1];
    const remaining = history.slice(0, -1);
    setHistory(remaining);

    if (last.action === 'ban') {
      if (last.team === 'radiant') {
        setRadiantBans((prev) => {
          const copy = [...prev];
          const idx = copy.lastIndexOf(last.hero);
          if (idx >= 0) copy.splice(idx, 1);
          return copy;
        });
      } else {
        setDireBans((prev) => {
          const copy = [...prev];
          const idx = copy.lastIndexOf(last.hero);
          if (idx >= 0) copy.splice(idx, 1);
          return copy;
        });
      }
    } else {
      if (last.team === 'radiant') {
        setRadiantPicks((prev) => {
          const copy = [...prev];
          const idx = copy.lastIndexOf(last.hero);
          if (idx >= 0) copy.splice(idx, 1);
          return copy;
        });
      } else {
        setDirePicks((prev) => {
          const copy = [...prev];
          const idx = copy.lastIndexOf(last.hero);
          if (idx >= 0) copy.splice(idx, 1);
          return copy;
        });
      }
    }
  }

  function resetDraft() {
    setRadiantBans([]);
    setDireBans([]);
    setRadiantPicks([]);
    setDirePicks([]);
    setHistory([]);
  }

  function heroObj(id) {
    return heroes.find((h) => h.id === id) || { id, name: id };
  }

  // suggestions aggregated for a team based on current picks
  function suggestionsForTeam(team) {
    const teamPicks = team === "radiant" ? radiantPicks : direPicks;
    const map = new Map();
    for (const pid of teamPicks) {
      const sy = SYNERGY_MAP[pid] || [];
      for (const sId of sy) {
        if (takenIds.has(sId)) continue;
        const curr = map.get(sId) || { score: 0, reasons: [] };
        curr.score += 1;
        curr.reasons.push(`Synergy with ${heroObj(pid).name}`);
        map.set(sId, curr);
      }
    }
    return [...map.entries()].map(([hero, val]) => ({ hero, ...val })).sort((a, b) => b.score - a.score);
  }

  // per-hero suggestions: return hero objects that synergize with this hero
  function suggestionsForHero(heroId) {
    const sy = SYNERGY_MAP[heroId] || [];
    return sy.map((id) => heroObj(id)).filter((h) => !takenIds.has(h.id));
  }

  // ----- SYNERGY EDITOR HELPERS -----
  function openEditorFor(heroId) {
    setEditingHero(heroId);
    setSynergyEditorOpen(true);
  }

  function toggleSynergy(targetHeroId) {
    setSynergyBase((prev) => {
      const copy = { ...prev };
      const arr = Array.isArray(copy[editingHero]) ? [...copy[editingHero]] : [];
      const idx = arr.indexOf(targetHeroId);
      if (idx >= 0) arr.splice(idx, 1);
      else arr.push(targetHeroId);
      copy[editingHero] = arr;
      return copy;
    });
  }

  function setEditorHero(hid) {
    setEditingHero(hid);
  }

  // ----- ORDER EDITOR HELPERS -----
  function addStep(type, team) {
    setOrder((prev) => [...prev, `${type}:${team}`]);
  }

  function removeStep(index) {
    setOrder((prev) => prev.filter((_, i) => i !== index));
  }

  function moveStep(index, dir) {
    setOrder((prev) => {
      const copy = [...prev];
      const target = index + dir;
      if (target < 0 || target >= copy.length) return prev;
      const tmp = copy[target];
      copy[target] = copy[index];
      copy[index] = tmp;
      return copy;
    });
  }

  function resetOrderToDefault() {
  setOrder(DEFAULT_ORDER);
}
  // counts for display
  const totalBans = order.filter((o) => o.startsWith('ban:')).length;
  const totalPicks = order.filter((o) => o.startsWith('pick:')).length;

  return (
    <div className="p-4 bg-gradient-to-br from-white to-slate-50 text-slate-900 min-h-screen">
      <div className="max-w-7xl mx-auto">
        <div className="flex items-center justify-between mb-6">
          <div>
            <h1 className="text-2xl font-bold">Dota 2 Captains Mode — Draft Simulator</h1>
            <div className="text-sm text-slate-600">Баны, пики и подсказки по синергиям для каждого героя.</div>
          </div>
          <div className="flex gap-2">
            <Button onClick={undoLast} variant="outline" disabled={history.length === 0}>Undo</Button>
            <Button onClick={resetDraft} variant="destructive">Сброс</Button>
            <Button onClick={() => setSynergyEditorOpen(true)} variant="outline">Редактировать синергии</Button>
            <Button onClick={() => setOrderEditorOpen(true)} variant="outline">Редактировать порядок ходов</Button>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          {/* Radiant column */}
          <div>
            <h2 className="text-lg font-semibold mb-2">Radiant</h2>
            <div className="mb-2 text-xs text-slate-500">Баны</div>
            <div className="flex gap-2 mb-3">
              {radiantBans.map((id) => (
                <div key={id} className="rounded bg-red-50 px-2 py-1 text-sm">{heroObj(id).name}</div>
              ))}
            </div>

            <div className="mb-2 text-xs text-slate-500">Пики</div>
            <div className="grid grid-cols-5 gap-2">
              {radiantPicks.map((id) => (
                <Card key={id}><CardContent className="flex flex-col items-center gap-1"><div className="h-12 w-12 flex items-center justify-center rounded bg-slate-100 text-sm font-bold">{initials(heroObj(id).name)}</div><div className="text-xs">{heroObj(id).name}</div></CardContent></Card>
              ))}
            </div>

            <div className="mt-4">
              <div className="text-xs font-semibold text-slate-500 mb-2">Рекомендации для Radiant</div>
              {suggestionsForTeam("radiant").length === 0 ? <div className="text-xs text-slate-400">Нет рекомендаций</div> : (
                <div className="space-y-2">
                  {suggestionsForTeam("radiant").map((s) => (
                    <div key={s.hero} className="flex items-center justify-between rounded border p-2">
                      <div>
                        <div className="text-sm font-medium">{heroObj(s.hero).name}</div>
                        <div className="text-xs text-slate-500">{(s.reasons || []).slice(0,2).join('; ')}</div>
                      </div>
                      <div className="text-sm text-emerald-700">{s.score}</div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* center controls */}
          <div>
            <div className="text-center mb-4">
              <div className="text-sm text-slate-500">Текущий этап: <strong className="capitalize">{currentAction ?? '—'}</strong></div>
              <div className="text-lg font-semibold">Ход: <span className="capitalize">{currentTeam ?? '—'}</span></div>
              <div className="mt-3 text-sm text-slate-600">Баны total: {totalBans} · Пики total: {totalPicks}</div>
              <div className="mt-1 text-sm text-slate-500">Прогресс: {stepIndex}/{order.length}</div>
            </div>

            <div className="mb-4">
              <h3 className="text-sm font-semibold mb-2">Рекомендации для текущей команды</h3>
              {suggestionsForTeam(currentTeam || 'radiant').length === 0 ? <div className="text-xs text-slate-400">Нет рекомендаций</div> : (
                suggestionsForTeam(currentTeam || 'radiant').map((s) => (
                  <div key={s.hero} className="flex items-center justify-between rounded border p-2 mb-2">
                    <div>
                      <div className="font-medium">{heroObj(s.hero).name}</div>
                      <div className="text-xs text-slate-500">{(s.reasons || []).slice(0,2).join('; ')}</div>
                    </div>
                    <div className="text-sm text-emerald-700">{s.score}</div>
                  </div>
                ))
              )}
            </div>

            <div className="flex gap-2 justify-center">
              <Button variant="outline" onClick={resetDraft}>Reset Draft</Button>
            </div>
          </div>

          {/* Dire column */}
          <div>
            <h2 className="text-lg font-semibold mb-2">Dire</h2>
            <div className="mb-2 text-xs text-slate-500">Баны</div>
            <div className="flex gap-2 mb-3">
              {direBans.map((id) => (
                <div key={id} className="rounded bg-red-50 px-2 py-1 text-sm">{heroObj(id).name}</div>
              ))}
            </div>

            <div className="mb-2 text-xs text-slate-500">Пики</div>
            <div className="grid grid-cols-5 gap-2">
              {direPicks.map((id) => (
                <Card key={id}><CardContent className="flex flex-col items-center gap-1"><div className="h-12 w-12 flex items-center justify-center rounded bg-slate-100 text-sm font-bold">{initials(heroObj(id).name)}</div><div className="text-xs">{heroObj(id).name}</div></CardContent></Card>
              ))}
            </div>

            <div className="mt-4">
              <div className="text-xs font-semibold text-slate-500 mb-2">Рекомендации для Dire</div>
              {suggestionsForTeam("dire").length === 0 ? <div className="text-xs text-slate-400">Нет рекомендаций</div> : (
                <div className="space-y-2">
                  {suggestionsForTeam("dire").map((s) => (
                    <div key={s.hero} className="flex items-center justify-between rounded border p-2">
                      <div>
                        <div className="text-sm font-medium">{heroObj(s.hero).name}</div>
                        <div className="text-xs text-slate-500">{(s.reasons || []).slice(0,2).join('; ')}</div>
                      </div>
                      <div className="text-sm text-emerald-700">{s.score}</div>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>
        </div>

        {/* hero pool */}
        <div className="mt-6">
          <h3 className="text-lg font-semibold mb-2">Пул героев</h3>
          <div className="grid grid-cols-6 gap-3 max-h-[420px] overflow-y-auto">
            {heroes.map((h) => {
              const disabled = takenIds.has(h.id) || (!currentOrderItem && (radiantPicks.length >= 5 && direPicks.length >= 5));
              const heroSy = (SYNERGY_MAP[h.id] || []).map((id) => heroObj(id));
              return (
                <motion.button
                  key={h.id}
                  onClick={() => assignHero(h.id)}
                  disabled={disabled}
                  whileHover={{ scale: disabled ? 1 : 1.03 }}
                  className={`rounded-lg border p-3 text-left bg-white ${disabled ? 'opacity-40 pointer-events-none' : 'hover:shadow'}
                  `}
                >
                  <div className="flex items-center gap-3">
                    <div className="h-12 w-12 flex items-center justify-center rounded bg-slate-100 text-sm font-bold">{initials(h.name)}</div>
                    <div className="flex-1">
                      <div className="font-semibold">{h.name}</div>
                      <div className="text-xs text-slate-500">{(h.roles || []).join(', ')}</div>
                      {/* per-hero synergy display */}
                      {heroSy.length > 0 && (
                        <div className="mt-2 flex gap-2 flex-wrap">
                          {heroSy.map((s) => (
                            <div key={s.id} className="rounded px-2 py-0.5 text-[11px] bg-emerald-50 text-emerald-700">+ {s.name}</div>
                          ))}
                        </div>
                      )}
                      <div className="mt-2 flex gap-2">
                        <Button size="sm" variant="outline" onClick={() => openEditorFor(h.id)}>Edit synergies</Button>
                      </div>
                    </div>
                  </div>
                </motion.button>
              );
            })}
          </div>
        </div>

        {/* Synergy editor modal */}
        {synergyEditorOpen && (
          <div className="fixed inset-0 z-50 grid place-items-center bg-black/40 p-4" onClick={() => setSynergyEditorOpen(false)}>
            <div className="max-h-[90vh] w-full max-w-3xl overflow-auto rounded-2xl bg-white p-4" onClick={(e) => e.stopPropagation()}>
              <div className="flex items-center justify-between mb-3">
                <div className="font-semibold">Редактор синергий</div>
                <div className="flex gap-2">
                  <Button variant="ghost" onClick={() => setSynergyEditorOpen(false)}>Close</Button>
                </div>
              </div>

              <div className="mb-3">
                <label className="text-xs text-slate-500">Hero to edit</label>
                <select value={editingHero} onChange={(e) => setEditorHero(e.target.value)} className="w-full rounded border p-2 mt-1">
                  {heroes.map((h) => <option key={h.id} value={h.id}>{h.name}</option>)}
                </select>
              </div>

              <div className="mb-3">
                <div className="text-sm font-medium mb-2">Toggle synergies (checked = good with selected hero)</div>
                <div className="grid grid-cols-3 gap-2 max-h-60 overflow-auto">
                  {heroes.filter((x) => x.id !== editingHero).map((h) => {
                    const checked = Array.isArray(synergyBase[editingHero]) && synergyBase[editingHero].includes(h.id);
                    return (
                      <label key={h.id} className={`flex items-center gap-2 rounded p-2 border ${checked ? 'bg-emerald-50' : ''}`}>
                        <input type="checkbox" checked={checked} onChange={() => toggleSynergy(h.id)} />
                        <div className="text-sm">{h.name}</div>
                      </label>
                    );
                  })}
                </div>
              </div>

              <div className="flex gap-2 justify-end">
                <Button onClick={() => setSynergyEditorOpen(false)}>Done</Button>
              </div>
            </div>
          </div>
        )}

        {/* Order editor modal */}
        {orderEditorOpen && (
          <div className="fixed inset-0 z-50 grid place-items-center bg-black/40 p-4" onClick={() => setOrderEditorOpen(false)}>
            <div className="max-h-[90vh] w-full max-w-3xl overflow-auto rounded-2xl bg-white p-4" onClick={(e) => e.stopPropagation()}>
              <div className="flex items-center justify-between mb-3">
                <div className="font-semibold">Редактор порядка ходов</div>
                <div className="flex gap-2">
                  <Button variant="ghost" onClick={() => setOrderEditorOpen(false)}>Close</Button>
                </div>
              </div>

              <div className="mb-3">
                <div className="text-sm font-medium mb-2">Текущий порядок ({order.length} шагов)</div>
                <div className="space-y-2">
                  {order.map((o, idx) => {
                    const [t, tm] = o.split(":");
                    return (
                      <div key={idx} className="flex items-center gap-2 rounded border p-2">
                        <div className="w-36 text-sm">{idx + 1}. {t.toUpperCase()} — <span className="capitalize">{tm}</span></div>
                        <div className="flex gap-1">
                          <Button size="sm" variant="outline" onClick={() => moveStep(idx, -1)}>↑</Button>
                          <Button size="sm" variant="outline" onClick={() => moveStep(idx, 1)}>↓</Button>
                          <Button size="sm" variant="destructive" onClick={() => removeStep(idx)}>Remove</Button>
                        </div>
                      </div>
                    );
                  })}
                </div>
              </div>

              <div className="mb-3">
                <div className="text-sm font-medium mb-2">Добавить шаг</div>
                <div className="flex gap-2 flex-wrap">
                  <Button onClick={() => addStep('ban', 'radiant')}>Ban Radiant</Button>
                  <Button onClick={() => addStep('ban', 'dire')}>Ban Dire</Button>
                  <Button onClick={() => addStep('pick', 'radiant')}>Pick Radiant</Button>
                  <Button onClick={() => addStep('pick', 'dire')}>Pick Dire</Button>
                  <Button variant="outline" onClick={resetOrderToDefault}>Reset to default</Button>
                </div>
              </div>

              <div className="flex gap-2 justify-end">
                <Button onClick={() => setOrderEditorOpen(false)}>Done</Button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
